.electronic-card .electronic-contentBox {
  position: relative;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2;
}

.electronic-card .electronic-contentBox h3 {
  font-size: 18px;
  color: white;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.electronic-card .electronic-contentBox .electronic-price {
  font-size: 24px;
  color: white;
  font-weight: 700;
  letter-spacing: 1px;
}

.electronic-card .electronic-contentBox .electronic-buy {
  position: relative;
  top: 100px;
  opacity: 0;
  padding: 10px 30px;
  margin-top: 15px;
  color: #000000;
  text-decoration: none;
  background: #ffce00;
  border-radius: 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: 0.5s;
}

.electronic-card:hover .electronic-contentBox .electronic-buy {
  top: 0;
  opacity: 1;
}

.electronic-mouse {
  height: 300px;
  width: auto;
}
